* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #eaeaea;
  overflow: hidden;
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1300px;
  height: 800px;
  background: #f5f5f5;
  box-shadow: 0 30px 50px #dbdbdb;
}

.video {
  position: absolute;
  box-shadow: 0 30px 50px #505050;
  background-position: 50% 50%;
  background-size: cover;
  display: inline-block;
  transition: 0.5s;

  top: 0;
  left: 0;
  transform: translate(0, 0);
  border-radius: 0;
  width: 100%;
  height: 100%;
}

.video .content {
  position: absolute;
  top: 50%;
  left: 100px;
  width: 300px;
  text-align: left;
  color: #eee;
  transform: translate(0, -50%);
  font-family: system-ui;
  display: block;
}

.video .content iframe {
  left: 100px;
  width: 600px;
  height: 468px;
  border-radius: 2rem;
}

.character {
  width: 200px;
  height: 300px;
  position: absolute;
  top: 30%;
  transform: translate(0, -50%);
  border-radius: 20px;
  box-shadow: 0 30px 50px #505050;
  background-position: 50% 50%;
  background-size: cover;
  display: inline-block;
  transition: 0.5s;
  overflow: hidden;
}

.character1 {
  left: 60%;
}

.character2 {
  left: calc(60% + 220px);
}

.character3 {
  top: 70%;
  left: 60%;
}

.character4 {
  top: 70%;
  left: calc(60% + 220px);
}

.card__article {
  margin-top: 300px;
}

.card__data {
  width: 200px;
  background-color: hsl(0, 0%, 100%);
  padding: 1.5rem 2rem;
  box-shadow: 0 8px 24px hsla(0, 0%, 0%, 0.15);
  border-radius: 1rem;
  bottom: -9rem;
  left: 0;
  right: 0;
  margin-inline: auto;
  opacity: 0;
  transition: opacity 1s 1s;
}

.card__description {
  display: block;
  font-size: 0.813rem;
  margin-bottom: 0.25rem;
}

.card__title {
  font-size: 1.25rem;
  font-weight: 500;
  color: hsl(0, 0%, 15%);
  margin-bottom: 0.75rem;
}

.card__button {
  text-decoration: none;
  font-size: 0.813rem;
  font-weight: 500;
  color: hsl(82, 60%, 28%);
}

.card__button:hover {
  text-decoration: underline;
}

.character:hover .card__article .card__data {
  animation: show-data 1s forwards;
  opacity: 1;
  transition: opacity 0.3s;
}

.character:hover {
  animation: remove-overflow 2s forwards;
}

.character:not(:hover) {
  animation: show-overflow 2s forwards;
}

.character:not(:hover) .card__article .card__data {
  animation: remove-data 1s forwards;
}

@keyframes show-data {
  50% {
    transform: translateY(-10rem);
  }
  100% {
    transform: translateY(-7rem);
  }
}

@keyframes remove-overflow {
  to {
    overflow: initial;
  }
}

@keyframes remove-data {
  0% {
    transform: translateY(-7rem);
  }
  50% {
    transform: translateY(-10rem);
  }
  100% {
    transform: translateY(0.5rem);
  }
}

@keyframes show-overflow {
  0% {
    overflow: initial;
    pointer-events: none;
  }
  50% {
    overflow: hidden;
  }
}

.button {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 20px;
}

.button button {
  width: 40px;
  height: 35px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  margin: 0 5px;
  transition: 0.3s;
}

.button button:hover {
  background: #ababab;
  color: #fff;
}
